"use client";
import React, { useEffect, useState } from "react";
import { useTemplateContext } from "../../useTemplateContext";
import { Template } from "../../page";
import { useParams } from "next/navigation";
const TemplatePage = () => {
  const { state } = useTemplateContext();
  const params = useParams();
  const [currentTemplate, setCurrentTemplate] = useState<Template>();
  const [isLoading , setisLoading] = useState<boolean>(true)
  const [error,setError] = useState<string>()
  useEffect(() => { 
    const fetchTemplate = async () => {
        try {
            setisLoading(true)
            const res = await fetch(`http://127.0.0.1:8001/api/templates/${params.id}`);
            const data = await res.json();
            setisLoading(false);
            setCurrentTemplate(data)
        } catch (error) {
            console.log(error);
            setError(error instanceof Error ? error.message : "An error occurred");
            setisLoading(false);
        }
    };
    fetchTemplate();
  }, []);
  return <div>{isLoading ? <div>Loading...</div> : error ? <div>{error}</div> : <div>
        <div dangerouslySetInnerHTML={{ __html: currentTemplate ? currentTemplate.content : ''}} />
    </div>}</div>;
};

export default TemplatePage;
